import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { TitleSize, TitleTag } from '../../atoms/title/title.types';
import { IconName } from '../../atoms/icon/icon.types';
import { SocialLinks } from './follow-us-banner.types';
import { ClickActionTarget } from '../../atoms/click-action/click-action.types';

/**
 * Follow us banner component.
 */
@Component({
  tag: 'bs-follow-us-banner',
  styleUrl: '../share-banner/share-banner.scss',
  shadow: true,
})
export class BsFollowUsBanner {
  /** Props */

  /** Section's title */
  @Prop() sectionTitle: string;

  /**
   * The social media links.
   */
  @Prop() socialLinks: SocialLinks[] | string;

  @Memoize('socialLinks')
  get parsedSocialLinks(): SocialLinks[] {
    return parseIfString(this.socialLinks);
  }

  render() {
    return (
      <Host>
        <div class="share-banner">
          {/* Title */}
          <bs-title text={this.sectionTitle} tag={TitleTag.H2} size={TitleSize.Head3} />

          {/* Social links */}
          <div class="social-section">
            {this.parsedSocialLinks.map((item) => (
              <div class="social-item">
                <bs-text class="text" text={item.type} />
                <div class="button-wrapper">
                  {item.instagramLink && (
                    <bs-click-action target={ClickActionTarget.Blank} href={item.instagramLink}>
                      <bs-icon name={IconName.SocialInstagram} />
                    </bs-click-action>
                  )}

                  {item.facebookLink && (
                    <bs-click-action target={ClickActionTarget.Blank} href={item.facebookLink}>
                      <bs-icon name={IconName.SocialFacebook} />
                    </bs-click-action>
                  )}

                  {item.youtubeLink && (
                    <bs-click-action target={ClickActionTarget.Blank} href={item.youtubeLink}>
                      <bs-icon name={IconName.SocialYoutube} />
                    </bs-click-action>
                  )}

                  {item.linkedinLink && (
                    <bs-click-action target={ClickActionTarget.Blank} href={item.linkedinLink}>
                      <bs-icon name={IconName.SocialLinkedin} />
                    </bs-click-action>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      </Host>
    );
  }
}
